// navbar
.wrapper {
    height: 64px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-right: 16px;
}

.link {
    display: flex;
    padding: 8px;
    text-transform: uppercase;
    text-decoration: none;
    background-image: linear-gradient(to right bottom, #c31432, #240b36);
   -webkit-background-clip: text;
   -moz-background-clip: text;
    background-clip: text;
   -webkit-text-fill-color: transparent;

    & + .link {
        margin-left: 16px;
    }

    &.active {
        border-bottom: 1px solid;
        border-image-slice: 1;
        border-image-source: linear-gradient(to right bottom, #6a5af9, orangered);
        background-image: linear-gradient(to right bottom, #6a5af9, red);
        -webkit-background-clip: text;
        -moz-background-clip: text;
         background-clip: text;
        -webkit-text-fill-color: transparent;
    }
}

.icon {
    margin-right: 4px;
    color: none;
    display: none;
}
