.carousel {
    height: calc(100vh - 64px);
    width: 100vw;
    background-image: linear-gradient(to top right, #ff9966, #ff5e62);
    padding: 32px;
    margin-bottom: 48px;
}

.container {
    flex-direction: row;
}

.category-sidebar {
    width: var(--sidebar-width);
    background-image: linear-gradient(to left bottom, rgb(255, 0, 0, 0.1), transparent);
}

.product-list {
    width: calc(var(--container-width) - var(--sidebar-width) - 16px * 3);
}

.title {
    background-image: unset;
    color: red;
    text-align: left;
}