@import 'normalize.css';

:root {
    --background-color: rgba(0,87,34,0.05);
    --color: rgb(51, 51, 51);
    --product-width: calc((100vw - 320px - 16px * 6) / 5);
    --container-width: 1240px;
    --sidebar-width: 240px;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

a[href] {
    text-decoration: unset;
}

// 
html {
    overflow: overlay;

    &::-webkit-scrollbar {
        border-radius: 0;
        width: 8px;
    }

    &::-webkit-scrollbar-thumb {
        border-radius: 4px;
        background-color: rgba(0, 0, 0, 0.15);
    }

    &::-webkit-scrollbar-track {
        border-radius: 0;
        background-color: rgba(0, 0, 0, 0);
    }
}

.app {
    position: relative;
}